<template>
  <h1>Calendario Dailys</h1>
  <div>
    <DatePicker v-model="date" mode="date" />
  </div>
  <div style="text-align: center; display: inline-block; padding-top: 20px">
    <table>
      <tr>
        <td style="text-align: right"><b>Responsable daily:</b></td>
        <td style="width: 200px" v-if="dayOfWeek()">
          {{ team[turn()].nombre }}
        </td>
        <td style="width: 200px" v-else>--</td>
        <!--<td><img :src="`${imagePerson(turn())}`" style="width: 60px;" /></td>-->
      </tr>
      <tr>
        <td style="text-align: right"><b>Backup responsable daily:</b></td>
        <td style="width: 200px" v-if="dayOfWeek()">
          {{ team[turnBackUp()].nombre }}
        </td>
        <td style="width: 200px" v-else>--</td>
        <!--<td><img :src="`${imagePerson(turnBackUp())}`" style="width: 60px;" /></td>-->
      </tr>
    </table>
  </div>
</template>

<script>
import { /*Calendar,*/ DatePicker } from "v-calendar";
import { ref } from "vue";
import Manfred from "../assets/Manfred.png";
import Lopez from "../assets/Lopez.png";
import Nogues from "../assets/Nogues.png";
import Abdo from "../assets/Abdo.png";
import Wells from "../assets/Wells.png";

export default {
  components: {
    //Calendar,
    DatePicker,
  },
  setup() {
    const team = [
      {
        nombre: "Juan Pablo Mora",
        turn: 0,
      },
      {
        nombre: "Trinidad Meneses",
        turn: 1,
      },
      {
        nombre: "Andrés Arancibia",
        turn: 2,
      },
      {
        nombre: "Abdo Anais",
        turn: 3,
      },
      {
        nombre: "Keynes Nogues",
        turn: 4,
      },
      {
        nombre: "Carlos Morales",
        turn: 5,
      },
      {
        nombre: "Juan Carlos López",
        turn: 6,
      },
      {
        nombre: "Armando Rojas",
        turn: 7,
      },
      {
        nombre: "Ian Wells",
        turn: 8,
      },
      {
        nombre: "Oscar Morales",
        turn: 9,
      },
    ];
    let date = ref(new Date());
    let dayOfYear = () =>
      Math.floor(
        (date.value - new Date(date.value.getFullYear(), 0, 0)) /
          (1000 * 60 * 60 * 24)
      );
    let dayOfWeek = () => date.value.getDay() > 0 && date.value.getDay() < 6;
    let turn = () => dayOfYear() % team.length;
    let turnBackUp = () =>
      turn() + 2 > 9 ? (turn() + 2) % team.length : turn() + 2;
    let imagePerson = (turn) => {
      switch (turn) {
        case 0:
          return Manfred;
        case 6:
          return Lopez;
        case 4:
          return Nogues;
        case 3:
          return Abdo;
        case 8:
          return Wells;
        default:
          return Manfred;
      }
    };

    return {
      date,
      dayOfYear,
      team,
      turn,
      turnBackUp,
      imagePerson,
      dayOfWeek,
    };
  },
};
</script>

